/* ---- MAIN ---- */

main {
  position: relative;
  min-height: 69vh;
  padding-bottom: 50px;
}

/* ---- 1st SECTION - DESCRIPTION ---- */

#intro {
  text-align: center;
  font-weight: bold;
}

/* ---- END 1st SECTION - DESCRIPTION ---- */

/* ---- 2nd SECTION ---- */

/* ---- COL ---- */

#button-col {
  text-align: center;
  z-index: 1;
  border: none;
  overflow: hidden;
  transition: color 0.4s ease-in-out;
}

/* ---- END COL ---- */

/* ---- BUTTON ---- */

.buttons {
  border: none;
  background: var(--main-color-maastricht_blue) !important;
  background: var(--main-color-maastricht_blue) !important;
  background: var(--main-color-maastricht_blue) !important;
  background: var(--main-color-maastricht_blue) !important;
  z-index: 1;
  position: relative;
  transition: color 0.4s ease-in-out;

  &:hover p {
    color: white;
  }

  &::before {
    content: "";
    z-index: -1;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    background-color: var(--main-color-heliotrope);
    transform-origin: center;
    transform: translate3d(-50%, -50%, 0) scale3d(0, 0, 0);
    transition: transform 0.45s ease-in-out;
  }

  &:hover {
    cursor: pointer;

    &::before {
      transform: translate3d(-50%, -50%, 0) scale3d(15, 15, 15);
    }
  }

  &:focus {
    border: none;
  }
}

/* ---- TEXT ---- */

.text {
  position: relative;
  top: 5px;
  color: var(--main-color-heliotrope);
  font-weight: bold;
  font-size: larger;
}

/* ---- ACTION CONTAINER ----*/

#actions-container {
  height: 250px;
  background: white;
  border: 3px black solid;
  display: flex;
  align-items: flex-end;
}

/* ---- TOOLTIP CONTAINER ---- */

.tooltip {
  position: relative;
  display: inline-block;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;

  .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: #555;
    color: #fff;
    text-align: center;
    padding: 5px 0;
    border-radius: 6px;

    /* ---- POSITION THE TOOLTIP TEXT ---- */
    position: absolute;
    z-index: 1;
    bottom: 125%;
    left: 50%;
    margin-left: -60px;

    /* ---- FADE IN TOOLTIP ---- */
    opacity: 0;
    transition: opacity 0.3s;

    &::after {
      content: "";
      position: absolute;
      top: 100%;
      left: 50%;
      margin-left: -5px;
      border-width: 5px;
      border-style: solid;
      border-color: #555 transparent transparent transparent;
    }
  }

  &:hover {
    background: var(--main-color-vivid_sky_blue) !important;
    cursor: pointer;

    .tooltiptext {
      visibility: visible;
      opacity: 1;
    }
  }
}

