/* ---- END Action Container ----*/

/* ---- END TEXT ---- */

/* ---- END BUTTON ---- */

/* ---- END 2nd SECTION ---- */

/* ---- END MAIN ---- */

/* ---- MODAL ---- */

.modal {
  display: none;

  /* Hidden by default */
  position: fixed;

  /* Stay in place */
  z-index: 1;

  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;

  /* Full width */
  height: 100%;

  /* Full height */
  overflow: auto;

  /* Enable scroll if needed */
  background-color: rgb(0, 0, 0);

  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);

  /* Black w/ opacity */
}

.modal-content {
  background-color: #fefefe;
  margin: 15% auto;

  /* 15% from the top and centered */
  border: 5px solid rgb(196, 190, 190);
  width: 50%;

  /* Could be more or less, depending on screen size */
  color: black;
  border-radius: 20px;
  text-align: center;
}

.close {
  color: white;
  float: right;
  font-size: 28px;
  font-weight: bold;
  position: relative;
  right: 5px;

  &:hover,
  &:focus {
    color: black;
    text-decoration: none;
    cursor: pointer;
  }
}

.modal-title {
  margin-top: 0;
  background-color: var(--main-color-vivid_sky_blue);
  border-radius: 14px 14px 0 0;
  padding: 2%;
}

.modal-content ul {
  list-style: none;
  padding-left: 0;
}

/* ---- END MODAL ---- */

@media (max-width: 525px) {
  .modal-content {
    width: 80%;
    margin-right: 13.9%;
  }
}

@media (max-width: 640px) {
  #button-col-reverse {
    margin-top: 4%;
  }
}
