* {
  box-sizing: border-box;
}

/* Grid measurements:
   *
   *   960px wide including 12 gutters (half gutters on both edges)
   *
   *   60px columns (12)
   *   20px gutters (two half-gutters + 11 full gutters, so 12 total)
   *
   *
   *   For smaller screens, we always want 20px of padding on either side,
   *   so 960 + 20 + 20 => 1000px
   *
   **/

.row {
  max-width: 1000px;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;

  &::before {
    display: table;
    content: "";
  }

  &::after {
    display: table;
    content: "";
    clear: both;
  }
}

/* Clearfix */

.col-1,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-10,
.col-11,
.col-12 {
  float: left;
  width: 100%;

  /* Gutters:
     * Each column is padded by half-a-gutter on each side,
     *
     * Half a gutter is 10px, 10/960 (context) = 1.041666%
     *
     */
  padding-left: 1.04166666%;
  padding-right: 1.04166666%;
}

/* ---- MARGIN RIGHT ---- */

.mr-1 {
  margin-right: 2%;
}

.mr-2 {
  margin-right: 4%;
}

.mr-3 {
  margin-right: 8%;
}

.mr-4 {
  margin-right: 16%;
}

/* ---- END MARGIN RIGHT ---- */

/* ---- MARGIN LEFT ---- */

.ml-1 {
  margin-right: 2%;
}

.ml-2 {
  margin-right: 4%;
}

.ml-3 {
  margin-right: 8%;
}

.ml-4 {
  margin-right: 16%;
}

/* ---- END MARGIN LEFT ---- */

/* ---- MARGIN TOP ---- */

.mt-1 {
  margin-top: 2%;
}

.mt-2 {
  margin-top: 4%;
}

.mt-3 {
  margin-top: 8%;
}

.mt-4 {
  margin-top: 16%;
}

/* ---- END MARGIN TOP ---- */

/* ---- MARGIN BOTTOM ---- */

.mb-1 {
  margin-bottom: 2%;
}

.mb-2 {
  margin-bottom: 4%;
}

.mb-3 {
  margin-bottom: 8%;
}

.mb-4 {
  margin-bottom: 16%;
}

/* ---- END MARGIN BOTTOM ---- */

/* ---- MARGIN RIGHT & LEFT ---- */

.mx-1 {
  margin: 0 2%;
}

.mx-2 {
  margin: 0 4%;
}

.mx-3 {
  margin: 0 8%;
}

.mx-4 {
  margin: 0 16%;
}

/* ---- END MARGIN RIGHT & LEFT ---- */

/* ---- MARGIN TOP & BOTTOM ---- */

.my-1 {
  margin: 2% 0;
}

.my-2 {
  margin: 4% 0;
}

.my-3 {
  margin: 8% 0;
}

.my-4 {
  margin: 16% 0;
}

/* ---- END MARGIN TOP & BOTTOM ---- */

/* ---- MARGIN TOP, RIGHT, BOTTOM & LEFT ---- */

.mxy-1 {
  margin: 2%;
}

.mxy-2 {
  margin: 4%;
}

.mxy-3 {
  margin: 8%;
}

.mxy-4 {
  margin: 16%;
}

/* ---- END MARGIN TOP, RIGHT, BOTTOM & LEFT ---- */

/* Non-mobile, grid */
@media only screen and (min-width: 640px) {
  /* 1 columns, 1/12 in % */

  .col-1 {
    width: 8.33%;
  }

  /* 2 columns, 2/12 in % */

  .col-2 {
    width: 16.66%;
  }

  /* 3 columns, 3/12 in % */

  .col-3 {
    width: 25%;
  }

  /* 4 columns */

  .col-4 {
    width: 33.33%;
  }

  .col-5 {
    width: 41.66%;
  }

  /* 6 columns */

  .col-6 {
    width: 50%;
  }

  /* 7 columns */

  .col-7 {
    width: 58.33%;
  }

  /* 8 columns */

  .col-8 {
    width: 66.66%;
  }

  /* 9 columns */

  .col-9 {
    width: 75%;
  }

  /* 10 columns */

  .col-10 {
    width: 83.33%;
  }

  /* 11 columns */

  .col-11 {
    width: 91.66%;
  }

  /* 12 columns */

  .col-12 {
    width: 100%;
  }
}
